import React from 'react';
import './index.css';
import Newbar from './Newbar';
import { NavLink } from 'react-router-dom';

const Common = (props) => {
    return(
        <>
        <div className="abc">
            <section id="header" className="d-flex align-items-center">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-10 mx-auto">
                        <div className="row">
                            <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1" id="content">
                                <h1>
                                    {props.name} <br/> I am <strong className="brand-name">Akhil Ankola</strong>
                                </h1>
                                <h2 className="my-3">
                                    {props.text}
                                </h2>
                                <br/>
                                <h4>
                                    {props.lasttext}
                                </h4>
                                <div className="mt-10">
                                    <NavLink to={props.visit} className="homebutton">
                                        {props.btname}
                                    </NavLink>

                                </div>
                            </div>

                            <div className="col-lg-6 order-1 order-lg-2 header-img">
                                <img src={props.imgsrc} 
                                className="img-fluid animated" alt="home img" />
                            </div>

                        </div>
                        </div>
                    </div>
                </div>
            </section> 
        </div>
        </>
    );
};
export default Common;